@keyframes Loading {
  0% {
    transform: rotate(0deg);
  }

  50% {
    transform: rotate(180deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@media screen and (max-width: 600px) {
  .HomePageNav {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background-color: #212529;
    width: 100%;
    height: 90px;
    color: white;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .HomePageLinkCont {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .HomePageLinkCont a {
    text-decoration: none;
    color: lightgray;
  }
  .HomePageLinkCont a:hover {
    color: white;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .LoginForm,
  .RegisterForm {
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 80%;
    margin-top: 15vh;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .LoginEmailDiv,
  .LoginPasswordDiv,
  .RegisterUsernameDiv,
  .RegisterEmailDiv,
  .RegisterPasswordDiv,
  .RegisterConfirmPasswordDiv {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .LoginEmailDiv input,
  .LoginPasswordDiv input,
  .LoginButton,
  .RegisterButton,
  .RegisterUsernameDiv input,
  .RegisterEmailDiv input,
  .RegisterPasswordDiv input,
  .RegisterConfirmPasswordDiv input {
    height: 40px;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .RegisterUl {
    list-style-type: none;
    opacity: 0.7;
  }
  .LoginWarning,
  .RegisterWarning {
    text-align: center;
    margin-top: 10vh;
    height: 70px;
    width: 60%;
    position: relative;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .CartUl {
    display: flex;
    flex-direction: column;
    list-style-type: none;
    margin-top: 10vh;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .CartUl li {
    display: flex;
    justify-content: space-between;
    border: 1px solid black;
    margin-top: 10px;
    transition: ease-in-out;
    transition-duration: 0.3s;
    font-weight: 600;
  }
  .CartUl img {
    height: 40px;
    width: auto;
  }
  .CartUl button {
    border-radius: 6px;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .CartCheckoutDiv {
    display: flex;
    flex-direction: column;
    margin-top: 40px;
    justify-content: space-evenly;
    align-items: center;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .CartCheckoutDiv a {
    text-decoration: none;
    border: 1px solid black;
    height: 60px;
    width: 200px;
    text-align: center;
    line-height: 40px;
    font-size: larger;
    margin-top: 5vh;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .ShoppingPageUl {
    display: grid;
    grid-template-columns: auto;
    padding-left: 7vw;
    padding-right: 7vw;
    box-sizing: border-box;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .ShoppingPageUl li {
    display: flex;
    flex-direction: column;
    margin-right: 4vw;
    margin-left: 4vw;
    margin-top: 8vh;
    border: 2px solid grey;
    padding: 10px;
    transition: ease-in-out;
    transition-duration: 0.3s;
    width: 250px;
  }
  .ShoppingPageUl li img,
  .ShoppingPageUl li p {
    margin-bottom: 30px;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .ShoppingPageUl li img {
    background-color: black;
    height: 150px;
    width: 100%;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .ShoppingPageFilterDiv {
    background-color: #212529;
    display: flex;
    justify-content: center;
    padding-top: 20px;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .ShoppingPageFilterDiv input {
    width: 40vw;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .ShoppingPageFilterDiv button {
    margin-right: 1vw;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .ShoppingPageFilterDiv select {
    width: 20vw;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .AddedToCart {
    text-align: center;
    margin-top: 3vh;
    height: 70px;
    width: 40%;
    position: relative;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .LogoutBtn {
    background-color: transparent;
    border: none;
    color: lightgray;
  }
  .LogoutBtn:hover {
    color: white;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .CheckoutContainer {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
    margin-top: 13vh;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .CheckoutUl {
    list-style-type: none;
    display: flex;
    width: 80%;
    flex-direction: column;
    transition: ease-in-out;
    transition-duration: 0.3s;
    border-top: 1px solid black;
    border-right: 1px solid black;
    border-left: 1px solid black;
    padding-left: 0;
  }
  .CheckoutUl li {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    margin-right: 2vw;
    transition: ease-in-out;
    transition-duration: 0.3s;
    border-bottom: 1px solid black;
  }
  .MainCheckoutContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .CheckoutBtn {
    width: 30%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 5vh;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .CheckedOutAlert {
    text-align: center;
    margin-top: 5vh;
    height: 13vh;
    width: 40%;
    position: relative;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .LoadingImage {
    height: 100px;
    margin-left: auto;
    margin-right: auto;
    display: block;
    animation-name: Loading;
    animation-timing-function: ease-in-out;
    animation-duration: 2s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
  }
  .LoadingContDiv {
    margin-top: 40vh;
  }
}
