  .ShoppingPageUl {
    display: grid;
    grid-template-columns: auto auto auto;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .ShoppingPageUl li {
    display: flex;
    flex-direction: column;
    margin-right: 2vw;
    margin-left: 2vw;
    margin-top: 8vh;
    border: 2px solid grey;
    padding: 10px;
    transition: ease-in-out;
    transition-duration: 0.3s;
    width: 18vw;
  }
  .ShoppingPageUl li img,
  .ShoppingPageUl li p {
    margin-bottom: 30px;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .ShoppingPageUl li img {
    background-color: black;
    height: 150px;
    width: 100%;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .ShoppingPageFilterDiv {
    background-color: #212529;
    display: flex;
    justify-content: center;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .ShoppingPageFilterDiv input {
    width: 30vw;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .ShoppingPageFilterDiv button {
    margin-right: 1vw;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .ShoppingPageFilterDiv select {
    width: 8vw;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .AddedToCart {
    text-align: center;
    margin-top: 3vh;
    height: 70px;
    width: 40%;
    position: relative;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .LogoutBtn {
    background-color: transparent;
    border: none;
    color: lightgray;
  }
  .LogoutBtn:hover {
    color: white;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .CheckoutContainer {
    display: flex;
    justify-content: space-between;
    width: 60%;
    align-items: center;
    margin-top: 13vh;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .CheckoutUl {
    list-style-type: none;
    display: flex;
    width: 60%;
    flex-direction: column;
    transition: ease-in-out;
    transition-duration: 0.3s;
    border-top: 1px solid black;
    border-right: 1px solid black;
    border-left: 1px solid black;
    padding-left: 0;
  }
  .CheckoutUl li {
    display: flex;
    justify-content: space-around;
    width: 100%;
    transition: ease-in-out;
    transition-duration: 0.3s;
    border-bottom: 1px solid black;
  }
  .MainCheckoutContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .CheckoutBtn {
    width: 10%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 5vh;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .CheckedOutAlert {
    text-align: center;
    margin-top: 5vh;
    height: 70px;
    width: 40%;
    position: relative;
    transition: ease-in-out;
    transition-duration: 0.3s;
  }
  .LoadingImage {
    height: 100px;
    margin-left: auto;
    margin-right: auto;
    display: block;
    animation-name: Loading;
    animation-timing-function: ease-in-out;
    animation-duration: 2s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
  }
  .LoadingContDiv {
    margin-top: 40vh;
  }
}
